import React, { useEffect, useState } from "react";
import { useRef } from "react";
import { ReactLenis, useLenis } from "lenis/react";
import {
  motion,
  useScroll,
  useTransform,
  useMotionTemplate,
  useMotionValueEvent,
  useElementScroll,
} from "framer-motion";
import { div } from "motion/react-client";
import Lenis from "lenis";
import Naivadyam1 from "../../utility/packages/Naivadyam-1.jpg";
import Naivadyam2 from "../../utility/packages/Naivadyam-2.jpg";
import Moveavableicon from "../../utils/moveavableicon";
import HeadName from "./HeadName";
const NewPackages = () => {
  const [isSmallScreen, setIsSmallScreen] = useState(false);
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth <= 740) {
        setIsSmallScreen(true); // Small screen (sm)
      } else {
        setIsSmallScreen(false); // Larger screens (md and up)
      }
    };

    handleResize(); // Check on initial render
    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  }, []);
  const SECTION_HEIGHT = isSmallScreen ? 500 : 1500;
  return (
    <ReactLenis root>
      <div>
        <Heading SECTION_HEIGHT={SECTION_HEIGHT} />
        <HeadName />
      
        {/* <NaivedyamContent />
      <BhojyamContent />
      <PanchabakshamContent/> */}
      </div>
    </ReactLenis>
  );
};
const Heading = ({ SECTION_HEIGHT }) => {
  const ref = useRef(null);
  const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["end start", "start end"],
  });
  const {scrollY} = useScroll()

  const y = useTransform(scrollY, [0, SECTION_HEIGHT-10], [600, 0]);
  const opacity = useTransform(scrollY, [SECTION_HEIGHT-10, SECTION_HEIGHT], [1, 0]);
  const scale = useTransform(scrollY, [ 0, SECTION_HEIGHT], [1.2, 1]);

  useMotionValueEvent(scrollY, "change", (latest) => {
    console.log(latest);
  });

  return (
    <motion.div
      className="w-screen bg-black "
      style={{
        height: `calc(${SECTION_HEIGHT}px  + 100vh)`,
      }}
    >
      <motion.div
        // ref= {ref}
        style={{}}
        className="w-full h-[100vh] sticky top-10 "
      >
        <motion.h1
          ref={ref}
          style={{
            y,
            opacity,
            scale,
          }}
          className="md:text-5xl text-sm text-white text-center"
        >
          LET ME TAKE YOU THROUGH THE PACKAGES
        </motion.h1>
      </motion.div>

      {/* <Naivedyam SECTION_HEIGHT={SECTION_HEIGHT} /> */}
    </motion.div>
  );
};

